// URL
export const BASE_URL = 'http://localhost:5000';

// 
export const getTasks = async (taskCircleId) => {
  try {
    const response = await fetch(`${BASE_URL}/tasks?task_circle_id=${taskCircleId}`);
    const data = await response.json();
    return Array.isArray(data) ? data : [];
  } catch (error) {
    console.error(':', error);
    return [];
  }
};

// 
export const createTask = async (taskName, taskCircleId) => {
  try {
    const response = await fetch(`${BASE_URL}/tasks/create`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        task_name: taskName,
        task_circle_id: taskCircleId
      })
    });
    if (!response.ok) {
      throw new Error(`: ${response.status}`);
    }
    return await response.json();
  } catch (error) {
    console.error(':', error);
    throw error;
  }
};

// 
export const createStep = async (stepData, taskId, task_circle_id) => {
  console.log(':', stepData, taskId, task_circle_id);
  try {
    // null
    const processedStepData = {
      ...stepData,
      startdate: stepData.startdate || null,
      enddate: stepData.enddate || null
    };

    const response = await fetch('http://localhost:5000/dashboard/create', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        ...processedStepData,
        task_id: taskId,
        task_circle_id: task_circle_id
      })
    });
    
    if (!response.ok) {
      const errorText = await response.text();
      console.error(':', response.status, errorText);
      throw new Error(`: ${response.status} ${errorText}`);
    }
    
    const data = await response.json();
    console.log(':', data);
    return data;
  } catch (error) {
    console.error(':', error);
    throw error;
  }
};

