// 
export const getTaskCircleStats = async (taskCircleId) => {
  try {
    const response = await fetch(`${BASE_URL}/task_circle/stats?id=${taskCircleId}`);
    if (!response.ok) throw new Error('');
    return await response.json();
  } catch (error) {
    console.error(':', error);
    return null;
  }
};